@import "../../../../styles/breakpoints.scss";
@import "../../../../styles/shadows.scss";

.s1-gradient-bg {
  display: block;
  position: relative;
  height: 100%;
  margin-top: -75px;
  padding-top: 75px;
  background: url("../../../../assets/texture/texture-bg.png"),
    linear-gradient(to bottom right, #060026, rgba(58, 52, 162, 0.83));
  overflow: hidden;
}

.s1 {
  display: block;
  position: relative;
  height: calc(100vh - 75px);

  & .cta-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    z-index: 5;
    padding: 60px 0 0 0;
    @include sm {
      padding: 100px 0 0 0;
    }
    @include lg {
      padding: 0;
      position: absolute;
      top: 40%;
      transform: translateY(-50%);
    }
    & .left {
      display: block;
      h1 {
        display: block;
        color: white;
        font-weight: 500;
        font-size: 40px;
        padding: 5px 0;
        margin: 0;
        @include sm {
          font-size: 50px;
        }
        @include lg {
          font-size: 60px;
        }
      }
      h2 {
        display: block;
        color: white;
        font-weight: 300;
        font-size: 17px;
        padding: 20px 0 0 0;
        margin: 0;
        line-height: 28px;
        @include sm {
          font-size: 21px;
          line-height: 38px;
        }
        @include md {
          width: 600px;
        }
        @include lg {
          font-size: 24px;
          line-height: 48px;
          width: 550px;
        }
      }
      & .cta-btn-container {
        display: grid;
        justify-content: center;
        position: relative;
        z-index: 5;
        margin: 60px 0 0 0;
        @include lg {
          margin: 80px 0 0 0;
        }
        @include sm {
          display: block;
        }
        & .cta-btn {
          display: inline-block;
          position: relative;
          color: #060026;
          background-color: white;
          // box-shadow: $shadow-regular-light;
          border: none;
          border-radius: 0.25rem;
          padding: 12px 32px;
          font-size: 14px;
          font-weight: 300;
          width: 180px;
          max-width: 180px;
          cursor: pointer;
          &.dark {
            color: white;
            background-color: #272727;
            box-shadow: $shadow-regular;
            width: 180px;
            margin-top: 20px;
            @include sm {
              margin-top: 0;
            }
            &:hover {
              box-shadow: $shadow-soft;
            }
          }
          &:hover {
            box-shadow: $shadow-soft-light;
          }
          &:first-child {
            margin-right: 15px;
          }
          @include md {
            font-size: 18px;
            padding: 12px 32px;
            &:first-child {
              margin-right: 25px;
            }
          }
        }
      }
    }
    & .right {
      & .phoenix-r {
        display: block;
        margin-left: -10%;
        object-fit: cover;
        opacity: 0.3;

        @include xl {
          margin-left: 25%;
        }
        @include xxl {
          margin-left: 30%;
          width: 110%;
        }
        @include xxxl {
          margin-left: 50%;
          width: 130%;
        }
      }
    }
  }

  & .logo-container {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    position: absolute;
    width: 100%;
    bottom: 25px;
    & .logo-logo {
      display: block;
      position: relative;
      z-index: 3;
      height: 25px;
      object-fit: contain;
      filter: grayscale(1);
      &.light {
        filter: grayscale(1) brightness(1.75);
      }
      &.lighter {
        filter: grayscale(1) brightness(3.5);
      }
      &:hover {
        filter: grayscale(0);
      }

      @include sm {
        height: 30px;
      }
      @include md {
        height: 35px;
      }
      @include lg {
        height: 40px;
      }
      @include xl {
        height: 45px;
      }
    }
  }

  & .phoenix-bg {
    display: block;
    position: absolute;
    z-index: 1;
    height: 100vh;
    bottom: 0;
    right: 0;
    transform: translate(50%, 22.5%);
    object-fit: cover;
    opacity: 0.15;
  }

  // & .texture-bg {
  //   display: block;
